"use client";

import { useState, useEffect } from "react";
import { useRouter } from "next/navigation";
import Navbar from "../../../components/Navbar";

export default function CreateTicketPage() {
  const router = useRouter();
  const [mounted, setMounted] = useState(false);
  const [user, setUser] = useState<any>(null);
  const [step, setStep] = useState(1);
  const [isSubmitting, setIsSubmitting] = useState(false);

  const departmentCategories = {
    IT: ["Network", "Hardware", "Software", "Account Reset"],
    GASO: ["Cleaning", "Electrical", "Plumbing", "Carpentry"],
    Nursing: ["Patient Care", "Medication", "Equipment", "Facility"],
    General: ["Concern", "Message", "Question", "Other"],
  };

  const [formData, setFormData] = useState({
    category: "",
    title: "",
    description: "",
    priority: "Medium",
  });

  // Pag-load ng page, set muna natin 'yung mga kailangan
  useEffect(() => {
    setMounted(true);
    // Check natin 'yung user info para malaman kung anong department siya
    const storedUser = localStorage.getItem("user");
    if (storedUser) {
      const parsedUser = JSON.parse(storedUser);
      setUser(parsedUser);
      // Auto-select natin 'yung category base sa department niya
      const defaultCat =
        departmentCategories[
          parsedUser.dept as keyof typeof departmentCategories
        ]?.[0] || "Other";
      setFormData((prev) => ({ ...prev, category: defaultCat }));
    } else {
      // Pag walang user, balik muna natin sa login page
      router.push("/login");
    }
  }, [router]);

  if (!mounted || !user) return <div className="min-h-screen bg-gray-50" />;

  // Dito natin mina-manage 'yung bawat input na binabago ni user
  const handleChange = (
    e: React.ChangeEvent<
      HTMLInputElement | HTMLTextAreaElement | HTMLSelectElement
    >,
  ) => {
    const { name, value } = e.target;
    setFormData((prev) => ({ ...prev, [name]: value }));
  };

  // Dito na natin isesend 'yung ticket sa database
  const handleSubmit = async () => {
    setIsSubmitting(true);
    try {
      const res = await fetch("http://localhost:3001/api/tickets", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({
          ...formData,
          createdBy: user.username,
          dept: user.dept,
        }),
      });
      if (res.ok) {
        alert("Ticket created successfully!");
        router.push("/tickets"); // Tapos na! Balik na tayo sa listahan ng tickets
      } else {
        alert("Failed to create ticket");
        setIsSubmitting(false);
      }
    } catch (error) {
      console.error(error);
      setIsSubmitting(false);
    }
  };

  return (
    <div className="min-h-screen bg-gray-50 font-sans">
      <Navbar user={user} />

      <main className="max-w-3xl mx-auto py-12 px-4 ">
        {/* Progress Indicator */}
        <div className="mb-8 flex items-center justify-center space-x-4 ">
          <div
            className={`h-2 w-24 rounded-full ${step >= 1 ? "bg-blue-600" : "bg-gray-200"}`}
          />
          <div
            className={`h-2 w-24 rounded-full ${step >= 2 ? "bg-blue-600" : "bg-gray-200"}`}
          />
        </div>

        <div className="bg-white shadow-2xl rounded-2xl overflow-hidden border border-gray-100">
          {/* Header */}
          <div className="bg-gradient-to-r from-blue-600 to-indigo-700 px-8 py-6">
            <h1 className="text-2xl font-extrabold text-white tracking-tight">
              {step === 1 ? "New Support Request" : "Confirm Your Details"}
            </h1>
            <p className="text-blue-100 text-sm mt-1">Step {step} of 2</p>
          </div>

          <div className="p-8">
            {step === 1 ? (
              <form
                onSubmit={(e) => {
                  e.preventDefault();
                  setStep(2);
                }}
                className="space-y-10"
              >
                {/* Category Selection - Dropdown */}
                <section>
                  <label className="text-sm font-semibold text-gray-900 uppercase tracking-wider mb-4 block">
                    1. Select Category
                  </label>
                  <div className="relative">
                    <select
                      name="category"
                      value={formData.category}
                      onChange={handleChange}
                      className="w-full rounded-xl border-gray-200 bg-gray-50 focus:bg-white focus:ring-4 focus:ring-blue-100 focus:border-blue-500 transition-all p-4 outline-none border appearance-none cursor-pointer"
                    >
                      {(
                        departmentCategories[
                          user.dept as keyof typeof departmentCategories
                        ] || departmentCategories.General
                      ).map((cat) => (
                        <option key={cat} value={cat}>
                          {cat}
                        </option>
                      ))}
                    </select>
                  </div>
                </section>

                {/* Subject & Description */}
                <section className="space-y-5">
                  <label className="text-sm font-semibold text-gray-900 uppercase tracking-wider block border-b pb-2">
                    2. Ticket Information
                  </label>

                  <div>
                    <label
                      htmlFor="title"
                      className="block text-xs font-bold text-gray-500 mb-1 ml-1"
                    >
                      SUBJECT TITLE
                    </label>
                    <input
                      type="text"
                      name="title"
                      id="title"
                      required
                      value={formData.title}
                      onChange={handleChange}
                      className="w-full rounded-xl border-gray-200 bg-gray-50 focus:bg-white focus:ring-4 focus:ring-blue-100 focus:border-blue-500 transition-all p-4 outline-none border"
                      placeholder="Enter a brief summary..."
                    />
                  </div>

                  <div>
                    <label
                      htmlFor="description"
                      className="block text-xs font-bold text-gray-500 mb-1 ml-1"
                    >
                      DETAILED DESCRIPTION
                    </label>
                    <textarea
                      name="description"
                      id="description"
                      rows={4}
                      required
                      value={formData.description}
                      onChange={handleChange}
                      className="w-full rounded-xl border-gray-200 bg-gray-50 focus:bg-white focus:ring-4 focus:ring-blue-100 focus:border-blue-500 transition-all p-4 outline-none border"
                      placeholder="Explain the issue in detail..."
                    />
                  </div>
                </section>

                {/* Priority Selection - Simple Dropdown */}
                <div className="space-y-8">
                  {/* Priority Section */}
                  <section>
                    <label className="block text-xs font-bold text-gray-500 mb-2 ml-1">
                      SET PRIORITY LEVEL
                    </label>
                    <div className="relative">
                      <select
                        name="priority"
                        value={formData.priority}
                        onChange={handleChange}
                        className="w-full rounded-xl border-gray-200 bg-gray-50 focus:bg-white focus:ring-4 focus:ring-blue-100 focus:border-blue-500 transition-all p-4 outline-none border cursor-pointer appearance-none"
                      >
                        {["Low", "Medium", "High", "Critical"].map((prio) => (
                          <option key={prio} value={prio}>
                            {prio}
                          </option>
                        ))}
                      </select>
                      <div className="absolute inset-y-0 right-0 flex items-center pr-4 pointer-events-none">
                        <svg
                          className="h-5 w-5 text-gray-400"
                          fill="none"
                          stroke="currentColor"
                          viewBox="0 0 24 24"
                        >
                          <path
                            strokeLinecap="round"
                            strokeLinejoin="round"
                            strokeWidth="2"
                            d="M19 9l-7 7-7-7"
                          />
                        </svg>
                      </div>
                    </div>
                  </section>

                  {/* Button Section */}
                  <div className="pt-10 mt-6 border-t border-gray-100">
                    <button
                      type="submit"
                      className="w-full bg-gradient-to-r from-blue-600 to-indigo-700 hover:from-blue-700 hover:to-indigo-800 text-white font-bold py-5 text-lg rounded-xl transition-all duration-200 shadow-[0_10px_20px_-10px_rgba(37,99,235,0.4)] hover:shadow-[0_15px_25px_-5px_rgba(37,99,235,0.4)] active:scale-[0.98] focus:outline-none focus:ring-2 focus:ring-blue-500 focus:ring-offset-2 border border-blue-400/20"
                    >
                      Continue to Review
                    </button>
                  </div>
                </div>
              </form>
            ) : (
              /* Review Section - Enhanced Spacing */
              <div className="space-y-8 animate-in fade-in slide-in-from-bottom-4 duration-500">
                <div className="bg-blue-50 border-l-4 border-blue-500 p-5 rounded-r-xl">
                  <p className="text-sm font-semibold text-blue-800 flex items-center">
                    Reviewing {user.dept} Request
                  </p>
                </div>

                <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
                  <div className="md:col-span-2 bg-gray-50 p-6 rounded-2xl border border-gray-100">
                    <h3 className="text-[10px] font-black text-gray-400 uppercase tracking-widest mb-2">
                      Subject
                    </h3>
                    <p className="text-xl font-extrabold text-gray-900 leading-tight">
                      {formData.title}
                    </p>
                  </div>

                  <div className="bg-gray-50 p-6 rounded-2xl border border-gray-100 text-center">
                    <h3 className="text-[10px] font-black text-gray-400 uppercase tracking-widest mb-2">
                      Priority
                    </h3>
                    <span
                      className={`px-4 py-2 rounded-full text-xs font-black uppercase ${
                        ["High", "Critical"].includes(formData.priority)
                          ? "bg-red-500 text-white"
                          : "bg-green-500 text-white"
                      }`}
                    >
                      {formData.priority}
                    </span>
                  </div>
                </div>

                <div className="bg-white rounded-2xl border-2 border-dashed border-gray-200 p-8">
                  <div className="flex justify-between items-center mb-4 border-b pb-4 border-gray-50">
                    <span className="text-xs font-bold text-gray-400 uppercase">
                      Description Details
                    </span>
                    <span className="text-xs font-bold text-blue-600 bg-blue-50 px-3 py-1 rounded-md">
                      Category: {formData.category}
                    </span>
                  </div>
                  <p className="text-gray-700 leading-loose text-lg whitespace-pre-wrap">
                    {formData.description || "No details provided."}
                  </p>
                </div>

                <div className="flex items-center justify-between pt-8 border-t border-gray-100">
                  <button
                    type="button"
                    onClick={() => setStep(1)}
                    className="text-gray-400 hover:text-gray-900 font-bold text-sm flex items-center gap-2 transition"
                    disabled={isSubmitting}
                  >
                    ‚Üê Back to Edit
                  </button>

                  <button
                    type="button"
                    onClick={handleSubmit}
                    disabled={isSubmitting}
                    className={`px-12 py-4 rounded-xl text-white font-bold transition-all shadow-xl active:scale-95 ${
                      isSubmitting
                        ? "bg-gray-400 cursor-not-allowed"
                        : "bg-green-600 hover:bg-green-700"
                    }`}
                  >
                    {isSubmitting ? "Submitting..." : "Confirm & Send Ticket"}
                  </button>
                </div>
              </div>
            )}
          </div>
        </div>
      </main>
    </div>
  );
}
